<script>
export default {
    name: 'VehicleVue',
    data() {
        return {
            xmlns: "https://www.w3.org/2000/svg",
            specialColor: '#F66B0E',
            eventNames: {
                clientWEB: {
                    Information: "ClientWEB:Vehicle:Information",
                }
            }
        }
    },
    unmounted() {
        if ("alt" in window) {
            alt.off(this.eventNames.clientWEB.Information, this.SetVehicleSpeedOmeterInformation);
        }
    },
    mounted() {
        if ("alt" in window) {
            alt.on(this.eventNames.clientWEB.Information, this.SetVehicleSpeedOmeterInformation);
        }
    },
    methods: {
        SetVehicleSpeedOmeterInformation(object) {
            if (!object.Status) {
                document.getElementById("vehicleInfoBox").classList.remove("vehicleInfoBoxActive");
            } else {
                document.getElementById("vehicleInfoBox").classList.add("vehicleInfoBoxActive");
            }
            document.getElementById(
                "VehicleInfoStrela"
            ).style.transform = `translate(-22px, 45px) rotate(${object.RPM}deg)`;
            document.getElementById("VehicleKMHValue").innerHTML = `${object.Speed}`;
            document.getElementById("VehicleFuelValue").style.width = `${object.Fuel}%`;
            object.Fuel <= 10
                ? document
                    .getElementById("VehicleFuelValue")
                    .classList.add("VehicleFuelValueAlert")
                : document
                    .getElementById("VehicleFuelValue")
                    .classList.remove("VehicleFuelValueAlert");
            object.Engine
                ? (document.getElementById("engine-icon").style.opacity = "1")
                : (document.getElementById("engine-icon").style.opacity = "0.35");

            object.TransitionStatus
                ? (document.getElementById(
                    "VehicleInfoStrela"
                ).style.transition = `0.3s`)
                : (document.getElementById(
                    "VehicleInfoStrela"
                ).style.transition = `1s`);
        }
    }
}
</script>

<style>
.vehicleInfoBox {
    opacity: 0;
    transition: opacity 0.3s;
}

.vehicleInfoBoxActive {
    opacity: 1;
    transition: opacity 0.3s;
}

.VehicleFuelValue {
    width: 0%;
}

.VehicleFuelValueAlert {
    animation: AlertFuelInSpeedOmeter 1s infinite;
}

.ActiveCarIcons {
    opacity: 1 !important;
}

#doors-icon,
/* Doors */
#belt-icon,
/* Kamar band */
#trunk-icon,
/* Tane */
#engine-icon,
/* engine */
#light-icon,
/* Cheragh */
#cruise-icon

/**/
    {
    transition: opacity 0.3s;
    display: block;
    opacity: 0.35;
}

/* #VehicleKMHValue */

.VehicleInfoStrela {
    /*  0 -> 10 */
    /*  1 -> 38 */
    /*  2 -> 64 */
    /*  3 -> 90 */
    /*  4 -> 116 */
    /*  5 -> 142 */
    /*  6 -> 168 */
    /*  7 -> 198 */
    /*  8 -> 226 */
    /*  9 -> 252 */
    /* 10 -> 275 - 280 */
    transition: 0.3s;
}

@font-face {
