/* speed motter kmh */
.kmh-number {
    display: flex;
    justify-content: center;
    position: absolute;
    width: 259.21px;
    font-family: Gilroy;
    font-style: normal;
    font-weight: bold;
    font-size: 85px;
    line-height: 103px;
    background: -webkit-linear-gradient(270deg,
            #ffffff 22.73%,
            rgba(255, 255, 255, 0) 78.51%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    top: 60px;
    z-index: 2;
}

.kmh {
    display: flex;
    justify-content: center;
    position: absolute;
    font-family: Acrom;
    font-style: normal;
    font-weight: bold;
    font-size: 25px;
    line-height: 30px;
    color: #ffffff;
    top: 125px;
    width: 259.21px;
    z-index: 3;
}

