    position: absolute;
    left: 54px;
    bottom: 20px;
    z-index: 1;
    transform: rotate(0deg);
}

/* car icons */
.car-icons {
    display: flex;
    flex-wrap: wrap;
    flex-basis: 25%;
    width: 81px;
