    background: #ffffff;
    filter: blur(15px);
    width: 90px;
    height: 13px;
    position: absolute;
    opacity: 0.2;
}

.fuel-bar {
    position: absolute;
    left: 15px;
    top: 5px;
    background: rgb(255 255 255 / 10%);
    width: 70px;
    height: 5px;
    border-radius: 50vh;
    overflow: hidden;
    z-index: 1;
}

.fuel-bar-inside {
    position: relative;
    background: linear-gradient(180.69deg,
            var(--Special_Color) 91.89%,
            var(--Special_Color) 6.08%);
    height: 100%;
    width: 0px;
    border-radius: 50vh;
    transition: width 1s;
    z-index: 10;
}

.fuel-bar-icon {
    position: absolute;
}

#beltColor path {
    fill: white;
}

#engineColor path {
    fill: white;
}
</style>